import React, { useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import { Box } from "@mui/material";
import { ImageWithFallback } from "../../utilities/ImageWithFallback";

const MenuTable = ({ menus, loading, selectionModel, setSelectionModel }) => {
  const [pageSize, setPageSize] = useState(10);

  const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
      field: "image_url",
      headerName: "ç”»åƒ",
      width: 120,
      renderCell: (params) => (
        <ImageWithFallback src={params.value} alt="menu" width={80} height={60} />
      ),
    },
    { field: "name", headerName: "å", width: 200 },
    {
      field: "category_name",
      headerName: "ã‚«ãƒ†ã‚´ãƒª",
      width: 150,
      valueGetter: (params) => params.row.category?.category_name || "",
    },
    {
      field: "sizes",
      headerName: "ã‚µã‚¤ã‚º / ä¾¡æ ¼",
      width: 250,
      valueGetter: (params) =>
        params.row.sizes?.map((s) => `${s.size}: ${s.price}å††`).join(", ") || "",
    },
    {
      field: "is_liked",
      headerName: "äººæ°—",
      width: 100,
      renderCell: (params) => (
        <input type="checkbox" checked={!!params.value} readOnly />
      ),
    },
    {
      field: "create_time",
      headerName: "ä½œæˆæ—¥ä»˜",
      width: 150,
      valueGetter: (params) => params.row.create_time?.slice(0, 10),
    },
  ];

  return (
    <Box sx={{ height: 650, width: "100%" }}>
      <DataGrid
        rows={menus || []}
        columns={columns}
        pageSize={pageSize}
        onPageSizeChange={(newPageSize) => setPageSize(newPageSize)}
        rowsPerPageOptions={[10, 50, 100]}
        pagination
        loading={loading}
        getRowId={(row) => row.id}
        checkboxSelection
        disableSelectionOnClick
        selectionModel={selectionModel}
        onSelectionModelChange={(newSelection) => {
          setSelectionModel(newSelection);
        }}
      />
    </Box>
  );
};

export default MenuTable;
