import React, { useState } from "react";

export const ImageWithFallback = ({ src, alt, width = 80, height = 60, style }) => {
  const [loaded, setLoaded] = useState(false);
  const [error, setError] = useState(false);

  const fallbackSrc = "https://placehold.co/80x60?text=Loading";
  const errorSrc = "https://placehold.co/80x60?text=No+Image";

  return (
    <img
      src={!error ? (loaded ? src : fallbackSrc) : errorSrc}
      alt={alt}
      width={width}
      height={height}
      style={{
        objectFit: "cover",
        borderRadius: 6,
        transition: "opacity 0.3s ease-in-out",
        boxShadow: "0 1px 4px rgba(0,0,0,0.15)",
        ...style,
      }}
      onLoad={() => setLoaded(true)}
      onError={() => setError(true)}
    />
  );
};
