import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
} from "@mui/material";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import { fetchMenu, deleteMenu } from "../../actions/CallApi/MenuFetch";
import MenuFilter from "../../components/MenuPage/MenuFillter";
import MenuTable from "../../components/MenuPage/MenuTable";
import { useAuth } from "../../context/AuthContext";

const MenuPage = () => {
  const [menu, setMenu] = useState([]);
  const [filterDate, setFilterDate] = useState("");
  const [loading, setLoading] = useState(false);
  const [selectionModel, setSelectionModel] = useState([]);
  const [errorModalOpen, setErrorModalOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [confirmModalOpen, setConfirmModalOpen] = useState(false);
  const { callApiWithToken } = useAuth();

  useEffect(() => {
    (async () => {
      setLoading(true);
      try {
        setMenu(await fetchMenu());
      } catch (err) {
        console.error("fetching error", err);
      } finally {
        setLoading(false);
      }
    })();
  }, []);


  const handleConfirmDelete = async () => {
    try {
      setLoading(true);
      await callApiWithToken(deleteMenu, { ids: selectionModel });
      setMenu((prev) =>
        prev.filter((item) => !selectionModel.includes(item.id))
      );
      setSelectionModel([]);
      setConfirmModalOpen(false);
    } catch (err) {
      setErrorMessage(err?.message || "å‰Šé™¤ã®å¤±æ•—.");
      setErrorModalOpen(true);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteSelected = () => {
    if (selectionModel.length === 0) return;
    setConfirmModalOpen(true);
  };

  const handleEdit = (navigate) => {
    if (selectionModel.length === 1) {
      const selectedItem = menu.find((m) => m.id === selectionModel[0]);
      navigate("form", { state: { initialData: selectedItem } });
    }
  };

  const filteredMenu = menu.filter((item) => {
    const matchDate = filterDate
      ? item.create_time.startsWith(filterDate)
      : true;
    return matchDate;
  });

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        ãƒ¡ãƒ‹ãƒ¥ãƒ¼ä¸€è¦§
      </Typography>

      <MenuFilter
        filterDate={filterDate}
        setFilterDate={setFilterDate}
        onReset={() => setFilterDate("")}
        selectionModel={selectionModel}
        onDeleteSelected={handleDeleteSelected}
        onEdit={handleEdit}
      />

      <MenuTable
        menus={filteredMenu}
        loading={loading}
        selectionModel={selectionModel}
        setSelectionModel={setSelectionModel}
      />

      <Dialog
        open={confirmModalOpen}
        onClose={() => setConfirmModalOpen(false)}
        aria-labelledby="confirm-dialog-title"
      >
        <DialogTitle
          id="confirm-dialog-title"
          sx={{ display: "flex", alignItems: "center", gap: 1 }}
        >
          <WarningAmberIcon color="warning" />
          ç¢ºèª
        </DialogTitle>
        <DialogContent>
          <Typography>
            {selectionModel.length} ä»¶ã®ãƒ¡ãƒ‹ãƒ¥ãƒ¼ã‚’å‰Šé™¤ã—ã¾ã™ã‹ï¼Ÿ
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setConfirmModalOpen(false)} color="inherit">
            ã‚­ãƒ£ãƒ³ã‚»ãƒ«
          </Button>
          <Button
            onClick={handleConfirmDelete}
            color="error"
            variant="contained"
            disabled={loading}
          >
            å‰Šé™¤
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={errorModalOpen}
        onClose={() => setErrorModalOpen(false)}
        aria-labelledby="error-dialog-title"
      >
        <DialogTitle
          id="error-dialog-title"
          sx={{ display: "flex", alignItems: "center", gap: 1 }}
        >
          <ErrorOutlineIcon color="error" />
          å‰Šé™¤ã‚¨ãƒ©ãƒ¼
        </DialogTitle>
        <DialogContent>
          <Typography color="error">{errorMessage}</Typography>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => setErrorModalOpen(false)}
            color="primary"
            autoFocus
          >
            ã‚ªãƒ¼ã‚±ãƒ¼
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default MenuPage;
